export default {
	data() {
		return {
			visible: false,
			title: null,
			message: null,
			width: null,
			showCancelButton: null,
			cancelButtonText: null,
			showConfirmButton: null,
			confirmButtonText: null,
			closeOnClickModal: null,
			beforeClose: null,
			callback: null,
			promise: {
				resolve: null,
				reject: null
			}
		};
	},

	methods: {
		open(options) {
			return new Promise((resolve, reject) => {
				this.promise = {
					resolve,
					reject
				};

				Object.assign(
					this,
					{
						title: '',
						message: '',
						width: '80%',
						showConfirmButton: true,
						showCancelButton: true,
						confirmButtonText: '确认',
						cancelButtonText: '取消',
						closeOnClickModal: true,
						callback: null,
						beforeClose: null
					},
					options
				);

				this.visible = true;
			});
		},

		close(action) {
			const done = () => {
				if (this.callback) {
					this.callback({ action });
				} else {
					if (action === 'confirm') {
						this.promise.resolve();
					} else {
						this.promise.reject(action);
					}
				}

				this.$nextTick(() => {
					this.visible = false;
				});
			};

			if (this.beforeClose) {
				this.beforeClose({ action, done });
			} else {
				done();
			}
		},

		onClose({ action }) {
			this.close(action);
		}
	}
};
